import React from 'react';

export interface Props {
    outlined?: boolean;
    onClick?: () => void;
    disabled?: boolean;
    children: React.ReactNode
}

export const Button: React.FC<Props> = ({ 
    children,
    outlined = false,
    ...props
 }) => {
    return (
        <button>
            {children}
        </button>
    )
}